section{
    border: 1px solid black;
    border-radius: 6px;
    width: 2in;
    padding: 5px;
    margin-left: 10px;
}
.header{
    height: 2in;
    background-image: url(landscape.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    position: relative;
}
.innerHead{
    position: absolute;
    background-color: rgba(0, 255, 255, 0.445);
    height: 2in;
    width: 100%;
}
.innerHead h1{
    font-size: 80px;
    display: flex;
    justify-content: center;
    margin-top: 40px;
    color: rgb(181, 0, 0);
}
.list{
    border: 1px solid black;
    border-radius: 6px;
    padding: 5px;
    display: flex;
    margin: 2px 0;
    font-size: 13px;
}
a{
    text-decoration: none;
    color: black;
}
.list:hover{
    background-color: rgb(0, 95, 95);
    color: white;
    cursor: pointer;
}
.count{
    display: flex;
    padding: 10px;
}
.detail{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
    border: 1px solid black;
    font-weight: 900;
    padding: 5px 20px;
    border-radius: 3px;
}
.hm-box{
    width: 100%;
    margin-top: 2px 0;
}
.intro{
    border: 2px solid black;
    border-radius: 6px;
    padding: 15px;
    text-align: justify;
    direction: rtl;
    margin-top: 5px;
}